.main {
	min-height: 100vh;
	padding: 30px 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--background);
	background: linear-gradient(
		130deg, 
		var(--basic-300-fade) 0%, 
		var(--background) 22%, 
		var(--background) 77%, 
		var(--second-accent-fade) 100%
	);
}

.main__wrapper {
	max-width: 550px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.main__theme-select {
	margin-bottom: 20px;
	margin-left: auto;
}