@charset "UTF-8";
@font-face {
  font-family: "Gilroy Light";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: local("Gilroy Light"), url("../fonts/gilroy-light.woff2") format("woff2"), url("../fonts/gilroy-light.woff") format("woff"); }

@font-face {
  font-family: "Gilroy Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Gilroy Regular"), url("../fonts/gilroy-regular.woff2") format("woff2"), url("../fonts/gilroy-regular.woff") format("woff"); }

@font-face {
  font-family: "Gilroy Bold";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Gilroy Bold"), url("../fonts/gilroy-bold.woff2") format("woff2"), url("../fonts/gilroy-bold.woff") format("woff"); }

/* Fonts */
/* Custom CSS Prop */
.body {
  /* Light Theme Colors */
  --basic-300: #7395cf;
  --basic-300-fade: rgba(115, 149, 207, 0.3);
  --basic-400: #3c5073;
  --accent-300: #92e2ff;
  --accent-400: #1dbef9;
  --accent: #1dbef9;
  --accent-600: #1dbef9;
  --accent-700: #19a2d4;
  --accent-fade: rgba(29, 190, 249, 0.3);
  --second-accent-300: #ffd099;
  --second-accent: #fe9b4b;
  --second-accent-fade: rgba(254, 155, 75, 0.3);
  --text: #45597a;
  --text-700: #1e2843;
  --text-700-fade: rgba(30, 40, 67, 0.3);
  --background-300: #f9f9f9;
  --background: #f0f0f1;
  --background-fade: rgba(240, 240, 241, 0.3);
  --background-accent: rgba(151, 97, 246, 0.2);
  --shadow-basic: #cccccd;
  --shadow-accent: #9fdcf8;
  --contrast: white;
  --decor-color: #e0e1e6; }

.body--dark {
  /* Dark Theme Colors */
  --basic-300-fade: #231664;
  --accent-300: #8668eb;
  --accent-400: #5841a5;
  --accent: #8668eb;
  --accent-600: #4f3b94;
  --accent-700: #473484;
  --second-accent-300: #b456c9;
  --second-accent-fade: #45233e;
  --text: #f9f9f9;
  --text-700: #f0f0f1;
  --text-700-fade: rgba(240, 240, 241, 0.3);
  --background: #07070c;
  --background-300: #21222f;
  --background-fade: rgba(7, 7, 12, 0.3);
  --shadow-basic: #221015;
  --shadow-accent: rgba(88, 65, 165, 0.5);
  --contrast: black;
  --decor-color: rgba(88, 65, 165, 0.6); }

*, *::before, *::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.body {
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "Gilroy Light", "Verdana", sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: var(--text); }
  .body--transition,
  .body--transition *,
  .body--transition .body::before,
  .body--transition .body::after {
    transition: background 0.3s, box-shadow 0.3s, border-color 0.3s, mask-image 0.3s; }

.title {
  position: relative;
  font-family: "Gilroy Bold", "Verdana", sans-serif;
  font-size: 24px;
  line-height: 30px;
  color: var(--text-700); }
  .title::after {
    position: absolute;
    content: "";
    bottom: -10px;
    left: 0;
    right: 0;
    height: 1px;
    border-radius: 2px;
    background-color: var(--decor-color); }

.subtitle {
  font-family: "Gilroy Bold", "Verdana", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: var(--accent); }

.transition {
  transition: all 0.5s; }

/* Blocks */
.main {
  min-height: 100vh;
  padding: 30px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--background);
  background: linear-gradient(130deg, var(--basic-300-fade) 0%, var(--background) 22%, var(--background) 77%, var(--second-accent-fade) 100%); }

.main__wrapper {
  max-width: 550px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.main__theme-select {
  margin-bottom: 20px;
  margin-left: auto; }

.theme {
  width: 100%;
  max-width: 140px;
  min-height: 50px;
  border-radius: 30px;
  background-color: var(--background-fade);
  box-shadow: 15px 15px 30px var(--shadow-basic); }

.theme__label {
  position: relative;
  display: block;
  width: 100%; }
  .theme__label::before, .theme__label::after {
    content: "";
    position: absolute;
    top: 15px;
    width: 20px;
    height: 20px;
    background-color: var(--text-700-fade); }
  .theme__label:before {
    left: 28px;
    mask-image: url("../img/light.svg"); }
  .theme__label:after {
    right: 28px;
    mask-image: url("../img/dark.svg"); }

.theme-input:checked + .theme__handler {
  left: calc(100% - 74px); }
  .theme-input:checked + .theme__handler .theme__handler-icon {
    mask-image: url("../img/dark.svg"); }

.theme__handler {
  position: absolute;
  z-index: 1;
  left: 4px;
  width: 70px;
  height: 42px;
  margin-top: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  background-color: var(--background-300);
  box-shadow: 2px 2px 10px var(--shadow-basic);
  transition: left 0.5s, right 0.5s; }

.theme__handler-icon {
  display: block;
  width: 20px;
  height: 20px;
  background-color: var(--text-700);
  mask-image: url("../img/light.svg");
  transition: background-color 0.3s, mask-image 0.3s; }

.todo {
  position: relative;
  width: 100%;
  border-radius: 30px;
  background-color: var(--background-300);
  box-shadow: 15px 15px 30px var(--shadow-basic);
  overflow: hidden; }
  .todo::before, .todo::after {
    content: "";
    position: absolute;
    width: 30%;
    filter: blur(60px); }
  .todo::before {
    height: 20%;
    bottom: 10%;
    left: 0;
    background-color: var(--second-accent-fade); }
  .todo::after {
    height: 40%;
    top: 40%;
    right: 0;
    background-color: var(--background-accent); }

.todo__wrapper {
  padding: 20px; }

.todo__title {
  margin-bottom: 30px; }

.todo__description {
  position: relative; }
  .todo__description::after {
    position: absolute;
    content: "";
    bottom: -10px;
    left: 0;
    right: 0;
    height: 1px;
    border-radius: 2px;
    background-color: var(--decor-color); }

.todo-list {
  position: relative;
  z-index: 2;
  padding: 20px 20px 40px;
  display: grid;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  border-top: 2px solid var(--contrast);
  background-color: var(--background-fade); }

.todo-list__list {
  margin-top: 0;
  margin-bottom: 40px;
  padding-left: 0;
  list-style: none; }

.todo-list__item:not(:last-child) {
  margin-bottom: 20px; }

.todo-list__add-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap; }

.todo-list__fieldset {
  position: relative;
  width: 100%;
  height: 0;
  margin: 0 0 20px;
  padding: 0;
  border: none;
  opacity: 0;
  visibility: hidden;
  transform: scale(0);
  transition: transform 0.3s, opacity 0.3s, height 0.3s; }
  .todo-list__fieldset:focus-within .todo-list__add-label,
  .todo-list__fieldset .todo-list__add-input:not(:placeholder-shown) + .todo-list__add-label {
    left: 30px;
    margin-top: -16px;
    font-size: 0.8em;
    color: var(--accent); }
  .todo-list__fieldset--shown {
    display: block;
    height: 62px;
    visibility: visible;
    opacity: 1;
    transform: none; }
    .todo-list__fieldset--shown .todo-list__add-input {
      box-shadow: 10px 10px 20px var(--shadow-basic); }

.todo-list__add-label {
  position: absolute;
  top: 20px;
  left: 20px;
  font-family: "Gilroy Regular", "Verdana", sans-serif;
  transition: all 0.3s; }

.todo-list__add-input {
  width: 100%;
  min-height: 62px;
  padding-top: 20px;
  padding-left: 30px;
  padding-right: 30px;
  border-radius: 30px;
  font-family: "Gilroy Regular", "Verdana", sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: var(--text);
  border: none;
  outline: none;
  box-shadow: none;
  background-color: var(--background-300);
  transition: box-shadow 1s; }

.todo-list__add-btn {
  position: relative;
  width: 52px;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Gilroy Regular", "Verdana", sans-serif;
  font-weight: 300;
  font-size: 0;
  line-height: 20px;
  color: white;
  border-radius: 30px;
  border: none;
  outline: none;
  background: linear-gradient(145deg, var(--accent-300), var(--accent-400));
  box-shadow: 3px 3px 6px var(--shadow-accent);
  cursor: pointer;
  transition: width 0.3s, font-size 0.3s; }
  .todo-list__add-btn:active {
    background: linear-gradient(145deg, var(--accent-600), var(--accent-600));
    box-shadow: inset 3px 3px 6px var(--accent-700), inset -3px -3px 6px var(--accent-300); }
    .todo-list__add-btn:active::before, .todo-list__add-btn:active::after {
      opacity: 0.8; }
  .todo-list__add-btn::before, .todo-list__add-btn::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 2px;
    border-radius: 2px;
    background-color: white;
    opacity: 1; }
  .todo-list__add-btn::after {
    transform: rotate(90deg); }
  .todo-list__add-btn--add {
    width: 120px;
    font-size: 16px; }
    .todo-list__add-btn--add::before {
      content: "Добавить";
      width: auto;
      height: auto;
      background-color: transparent; }
    .todo-list__add-btn--add::after {
      content: none; }

.task {
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 30px;
  box-shadow: 10px 10px 20px var(--shadow-basic);
  background-color: var(--background-300);
  cursor: pointer;
  opacity: 1;
  transform: none; }
  .task--hide {
    transform: scale(0);
    box-shadow: none;
    opacity: 0; }
  .task--transition {
    transition: transform 0.3s, box-shadow 1s, opacity 0.3s;
    transition-delay: 0.5s; }

.task__title {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Gilroy Regular", "Verdana", sans-serif; }

.task__range {
  display: block;
  position: relative;
  width: 30px;
  height: 15px;
  border-radius: 10px;
  background-color: #d5dce3;
  cursor: pointer;
  transition: background-color 0.3s; }

.task__handler {
  position: absolute;
  top: -3px;
  left: 0;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  box-shadow: 3px 3px 6px var(--shadow-basic);
  background-color: var(--contrast);
  cursor: pointer;
  transition: left 0.3s, box-shadow 0.3s; }

.task__input:checked + .task__range {
  background-color: var(--accent); }
  .task__input:checked + .task__range .task__handler {
    left: calc(100% - 20px);
    box-shadow: 3px 3px 6px var(--accent-300); }
